/* Styling logo */
.logo img {
    height: auto;
    width: 120px;
}

/* Styling header secara keseluruhan */
/* Header styling */
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 100px 20px 40px;
    background: linear-gradient(to right, #B4272F, #29226E);
    color: white;
    position: relative;
    z-index: 10;
}

/* Styling untuk elemen menu di dalam header */
.nav-links li {
    position: relative;
}

/* Styling link menu */
.nav-links a {
    text-decoration: none;
    color: white;
    font-weight: normal;
    transition: color 0.3s ease;
}

/* Menandakan menu yang sedang aktif */
.nav-links .active {
    font-weight: bold;
}

.nav-links .button {
    margin-left: 20px;
}

/* Styling untuk menu navigasi utama */
/* Navigation links styling */
.nav-links ul {
    list-style: none;
    display: flex;
    grid-gap: 30px;
    gap: 30px;
    margin: 0;
    padding: 0;
}

/* Dropdown Container */
.nav-links li {
    position: relative;
}

.nav-links a {
    text-decoration: none;
    color: white;
    font-weight: normal;
    transition: color 0.3s ease;
}

/* Active link styling */
.nav-links a.active {
    font-weight: bold;
}

.button.active {
    font-weight: bold;
    color: white;
}

.dropdown {
    position: relative;
    cursor: pointer;
}

.dropdown-menu {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    background-color: white;
    color: black;
    list-style: none;
    padding: 10px 0;
    margin: 0;
    border-radius: 4px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
    z-index: 10;
    min-width: 220px;
    text-align: left;
}

.dropdown:hover .dropdown-menu {
    display: block;
    border-radius: 4px;
}

.dropdown-menu li {
    margin: 0;
    padding: 0;
    border-radius: 4px;
}

.dropdown-menu li a {
    text-decoration: none;
    color: black;
    font-weight: 500;
    padding: 10px 20px;
    display: block;
    border-radius: 4px;
    transition: background-color 0.3s ease, color 0.3s ease;
}

.dropdown-menu li a:hover {
    background-color: #f4f4f4;
    color: #29226E;
    border-radius: 4px;
}

/* Styling the button */
/* Button styling */
.button {
    background-color: #B7262B;
    color: white;
    padding: 12px 25px;
    border-radius: 20px;
    font-size: 16px;
    text-decoration: none;
    transition: background-color 0.3s ease, transform 0.3s ease;
    border: none;
    cursor: pointer;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    display: inline-block;
    line-height: 1;
    margin-top: -5px;
}

/* Hover effect for button */
.button:hover {
    background-color: #9B1E22;
    transform: translateY(-2px);
    box-shadow: 0 6px 10px rgba(0, 0, 0, 0.15);
}

/* Active state for button */
.button:active {
    transform: translateY(0);
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.1);
}


/* Responsive Design */
@media (max-width: 768px) {
    .header {
        flex-direction: column;
        align-items: center;
        text-align: center;
    }

    .nav-links ul {
        flex-direction: column;
        grid-gap: 15px;
        gap: 15px;
    }

    .dropdown-menu {
        position: relative;
    }
}
