.import-image-sea {
  width: 1200px;
  padding-top: 30px;
  padding-bottom: 50px;
  z-index: 20;
  transition: transform 0.7s ease, box-shadow 0.7s ease;
}

.import-image-sea:hover {
  transform: translateY(-10px) scale(1.25);
}


/* Details Section Styles */
.details-section-sea {
  background: white;
  text-align: center;
  padding: 1% 9%;
  padding-top: 50px;
  padding-left: 30px;
  padding-right: 30px;
  font-size: 16px;
}

.details-section-sea p {
  max-width: 1000px;
  text-align: center;
  line-height: 1.6;
  margin: 0 auto;
  padding-bottom: 25px;

}

/* Freight Types Section */
.freight-types {
  display: flex;
  justify-content: center;
  grid-gap: 30px;
  gap: 30px;
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 20px;
}

.freight-type {
  flex: 1 1;
  max-width: 500px;
  padding: 20px;
  background: #AEC7DF;
  border-radius: 8px;
  transition: transform 0.5s ease, box-shadow 0.5s ease;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.freight-type:hover {
  transform: translateY(-15px) scale(1.05);
  box-shadow: 0 8px 16px rgba(75, 119, 160, 0.397);
}

.freight-type h3 {
  color: black;
  font-weight: bold;
  margin-bottom: 15px;
  font-size: 20px;
}

.freight-type p {
  color: black;
  font-weight: lighter;
  line-height: 1.5;
}

/* Responsive Design */
@media (max-width: 768px) {
  .image {
    height: 300px;
  }

  .bg-sea-truck {
    height: 300px;
  }

  .freight-types {
    flex-direction: column;
    grid-gap: 20px;
    gap: 20px;
  }

  .tagline-import {
    font-size: 32px;
    /* Adjust size for smaller screens */
  }

  .import-image {
    width: 100%;
    /* Full width on smaller screens */
    margin: 20px 0 0;
  }
}
/* Global Styles */
.image-container {
  position: relative;
  width: 100%;
}

.image-customs {
  width: 100%;
  object-fit: cover;
}

/* Hero Section */
.bg-customs {
  height: 450px; /* Adjusted height */
}

.no-underline {
  text-decoration: none; /* Removes underline from the tagline */
}

/* Details Section */
.details-section {
  padding: 50px 20px;
  background-color: white;
  text-align: center;
}

.details-section p {
  max-width: 800px;
  margin: 0 auto;
  font-size: 18px;
  line-height: 1.8;
}

/* bgClear Section */
.bg-clear-container {
  position: relative;
  width: 100%;
  height: auto;
  margin-top: 20px;
}

.bg-clear {
  height: 500px; /* Adjusted height for bgClear */
  width: 100%;
  object-fit: cover;
}

.bg-clear-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  grid-gap: 60px;
  gap: 60px; /* Larger space between bgDeal and text content */
  align-items: center;
  padding: 20px;
  z-index: 2;
  color: white;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 98.5%;
  background-color: rgba(0, 0, 0, 0.5);
}

.overlay-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  width: 100%;
  color: white;
  z-index: 1;
}

/* Deal Image (bgDeal) Styling */
.deal-image-wrapper {
  flex: 1 1;
  max-width: 450px; /* Larger size for bgDeal */
  height: 100%; /* Ensure consistent height */
  margin-left: 30px; /* Add larger left margin for spacing */
}

.bg-deal {
  width: 100%;
  height: auto;
  object-fit: cover;
}

/* Text Content */
.text-content {
  flex: 2 1;
  font-size: 16px;
  line-height: 1.8; /* Match line height for better alignment */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-right: 30px; /* Add larger right margin */
  max-width: calc(100% - 480px); /* Dynamically adjust width to match bgDeal */
}

.text-content p {
  margin-bottom: 20px;
  text-align: justify; /* Align text for a cleaner look */
}

/* Button Styling */
.button-wrapper {
  text-align: right; /* Align button to the right */
  margin-top: 35px; /* Move button slightly downward */
}

.contact-button {
  padding: 8px 16px; /* Adjust button padding */
  background-color: #B8272C; /* Red button */
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 4px;
  font-size: 19px; /* Adjust button text size */
}

.contact-button:hover {
  background-color: #cc0000; /* Darker red on hover */
}

/* Responsive Design */
@media (max-width: 768px) {
  .bg-clear-content {
    flex-direction: column;
    text-align: center;
    grid-gap: 20px;
    gap: 20px; /* Reduce space between elements */
  }

  .deal-image-wrapper {
    max-width: 250px; /* Reduce bgDeal size for smaller screens */
    margin-left: 0; /* Remove left margin on small screens */
  }

  .text-content {
    font-size: 14px; /* Adjust font size for smaller screens */
    text-align: center;
    margin-right: 0; /* Remove right margin */
  }

  .button-wrapper {
    text-align: center; /* Center the button */
    margin-top: 15px; /* Reduce spacing */
  }

  .contact-button {
    font-size: 14px; /* Smaller button text */
    padding: 6px 12px; /* Adjust padding for smaller screens */
  }
}

